.default {
  --background-color: white;
  --semitransparent-background: rgba(255, 255, 255, 0.85);
  --border-color: rgb(239, 243, 244);
  --hover-color: #E7E7E8;
  --font-color: rgb(15, 20, 25);
  --light-font-color: rgb(83, 100, 113);
  --button-color: #E7E7E8;
}

.dim {
  --background-color: rgb(21, 32, 43);
  --semitransparent-background: rgba(21, 32, 43, 0.85);
  --border-color: rgb(56, 68, 77);
  --font-color: rgb(247, 249, 249);
  --hover-color: rgb(48, 57, 68);
  --light-font-color: rgb(139, 152, 165);
  --button-color: rgb(247, 249, 249);
}

.lights-out {
  --background-color: rgb(0, 0, 0);
  --semitransparent-background: rgba(0, 0, 0, 0.85);
  --border-color: rgb(47, 51, 54);
  --font-color: rgb(231, 233, 234);
  --hover-color: rgb(24, 24, 24);
  --light-font-color: rgb(113, 118, 123);
  --button-color: rgb(247, 249, 249);
}

.blue-color {
  --color: rgb(29, 155, 240);
}

.yellow-color {
  --color: rgb(255, 212, 0);
}

.pink-color {
  --color: rgb(249, 24, 128);
}

.purple-color {
  --color: rgb(120, 86, 255);
}

.orange-color {
  --color: rgb(255, 122, 0);
}

.green-color {
  --color: rgb(0, 186, 124);
}

textarea {
  outline: none;
  border: none;
  color: var(--font-color);
}

.app-wrap {
  background-color: var(--background-color);
}

.App {
  justify-content: center;
  text-align: center;
  display: flex;
  width: clamp(0px, 100%, 1320px);
  margin: auto;
  min-height: 100vh;
}

h1 {
  color: var(--font-color);
}

.App>* {
  background-color: var(--background-color);
}

main {
  border-right: 1px solid var(--border-color);
  color: var(--font-color);
  width: 600px;
  flex-direction: column;
  display: flex;
}

nav {
  border-right: 1px solid var(--border-color);
  flex: 1;
  text-align: left;
  padding: 10px 30px;
  max-width: 275px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  height: 100vh;
}

.right-bar {
  flex: 1;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  height: 100vh;
}

.nav-links {
  width: 100%;
  padding: 0;
  margin-top: 8px;
  font-size: 1.4rem;
  list-style-type: none;
  justify-content: flex-start;
  display: flex;
  flex-direction: column;
  gap: 8px;
  transition: 200ms;
}

.nav-links a {
  width: fit-content;
  display: flex;
  width: 100%;
  align-items: center;
  gap: 15px;
  color: var(--font-color);
  text-decoration: none;
  padding: 12px;
  transition: 300ms;
  border-radius: 50px;
}

.nav-links a:hover {
  background-color: var(--hover-color);
}

nav svg {
  height: 30px;
  fill: var(--font-color);
}


.logo svg {
  fill: var(--color);
  stroke: none;
}

.logo {
  padding: 12px;
}

.active svg {
  fill: var(--font-color);
  stroke: none;
}

li:nth-child(2) .active svg {
  height: 30px;
  fill: white;
  stroke: var(--font-color);
  stroke-width: 2px;
}

.nav-links .active {
  font-weight: 600;
}

.tweet-button {
  background-color: var(--color);
  color: white;
  font-size: 1.2rem;
  border: none;
  border-radius: 50px;
  width: 100%;
  font-family: inherit;
  padding: .95rem 3rem;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.tweet-button svg {
  display: none;
  height: 30px;
}

.sidebar-text-node {
  padding-right: 10px;
  width: 100%;
  overflow: hidden;
  color: var(--font-color);
  text-overflow: ellipsis;
}

@media (max-width: 1281px) {
  .sidebar-text-node {
    display: none;
  }

  nav {
    max-width: 210px;
  }

  .nav-links a {
    justify-content: flex-end;
    padding: .75rem;
    margin-left: auto;
  }

  .tweet-button svg {
    display: block;
    fill: white;
  }

  .tweet-button {
    display: flex;
    align-items: flex-end;
    padding: .75rem;
    width: auto;
    margin-left: auto;
    text-align: right;
  }

  .logo {
    text-align: right;
  }

  nav {
    padding: 10px 20px;
    max-width: 94px;
  }
}

@media (max-width: 1100px) {
  nav {
    max-width: 190px;
  }
}

@media (max-width: 999px) {

  .nav-links a {
    justify-content: flex-end;
    padding: .75rem;
    margin-left: auto;
  }

  .tweet-button svg {
    display: block;
    fill: white;
  }

  .tweet-button {
    display: flex;
    align-items: flex-end;
    padding: .75rem;
    width: auto;
    margin-left: auto;
    text-align: right;
  }


  nav {
    padding: 10px 20px;
  }
}

.loader-spinner {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  align-self: center;
  margin-top: 10% !important;
  border: 3px solid var(--color);
  border-bottom: 3px solid white;
  animation: spinning 2s infinite linear;
}

@keyframes spinning {
  0% {
    transform: rotate(0deg);
  }

  30% {
    transform: rotate(180deg);
  }

  50% {
    transform: rotate(360deg);
  }

  60% {
    transform: rotate(540deg);
  }

  70% {
    transform: rotate(720deg);
  }

  80% {
    transform: rotate(900deg);
  }

  100% {
    transform: rotate(1080deg);
  }
}

.heightened .banner {
  position: relative;
}

.emoji {
  padding: 0px 1px;
}

.modal-container {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  height: 100%;
  background-color: rgba(63, 63, 63, 0.322) !important;
  z-index: 100;
}

.modal {
  padding: 2px;
  margin-top: 80px;
  background-color: var(--background-color);
  border-radius: 16px;
  max-height: 600px;
  overflow: auto;
  overflow: hidden;
  padding-top: 0px;
}

.close-modal-svg {
  height: 36px;
  padding: 8px;
  fill: var(--font-color);
  border-radius: 50%;
}

.close-modal {
  width: fit-content;
  height: 36px;
}

.close-modal-svg:hover {
  background-color: var(--hover-color);
}

.modal .tweet {
  border: 0px;
}

.home-modal .tweet-write-content__control {
  min-height: 100px;
  border-bottom: 1px solid var(--border-color);
}

.modal-header {
  display: flex;
  gap: 15px;
  align-items: center;
  padding: 12px;
  position: sticky;
  top: 0px;
  background-color: var(--semitransparent-background);
  backdrop-filter: blur(8px);
  z-index: 10;
}

.modal-flex {
  display: flex;
  flex: 1;
  justify-content: space-between;
  align-items: center;
}

.modal-title {
  font-size: 1.3rem;
  font-weight: 600;
}

.save-button {
  background-color: black;
  color: white;
  font-size: 1.03rem;
  font-weight: 600;
  padding: .35rem 1rem;
  border-radius: 25px;
  font-family: inherit;
  transition: 200ms;
  border: none;
}

.save-button:hover {
  background-color: #282c34;
}

.centered {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

/* 720px */

nav .tweet-header {
  flex-direction: column;
  gap: 0px;
}

.input-file {
  visibility: hidden;
  position: absolute;
}

.popup {
  position: fixed;
  bottom: 10px;
  z-index: 10000;
  background-color: var(--color);
  padding: 5px 30px;
  color: white;
  border-radius: 5px;
  animation: apear 500ms linear;
}

@keyframes apear {
  from {
    transform: translateY(5px);
    opacity: 0;
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.tweet {
  position: relative;
}