.banner {
    height: 200px;
    min-height: 200px;
    background-color: orangered;
}

.calendar-svg {
    width: 18.75px;
    fill: var(--light-font-color);
    height: 18.75px;
    margin-right: 4px;
}

.profile-header {
    display: flex;
    flex-direction: column;
    letter-spacing: -.5px;
    align-items: flex-start;
}

.profile-tweets-count {
    font-size: .95rem;
    color: var(--light-font-color);
    font-weight: 400;
}

.profile-opt {
    display: flex;
    height: 68px;
    position: relative;
    align-items: flex-start;
    justify-content: flex-end;
}

.profile-pic-container {
    bottom: 0px;
    left: 0px;
    position: absolute;
    margin-bottom: 12px;
    object-fit: cover;
    height: 142px;
    width: 142px;
    border-radius: 50%;
    padding: 4px;
    background-color: var(--background-color);
}

.minimized {
    width: 125px;
    height: 125px;
}

.profile-pic {
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.svg-modal {
    height: 45px;
    width: 45px;
    padding: 10px;
    border-radius: 50%;
    fill: rgb(241, 237, 237);
    background-color: rgba(0, 0, 0, 0.527);
    transition: 300ms;
}

.svg-modal:hover {
    background-color: rgba(0, 0, 0, 0.411);
}

.edit-profile {
    color: var(--font-color);
    padding: .45rem .95rem;
    border-radius: 50px;
    font-weight: 600;
    font-size: 1rem;
    font-family: inherit;
    border: 1px solid rgb(212, 212, 212);
    background-color: transparent;
    transition: 300ms;
}

.edit-profile:hover {
    background-color: rgba(220, 220, 220, 0.466);
}

.profile {
    padding: 12px;
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.profile-ids {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    letter-spacing: -.5px;
}

.profile-name {
    font-size: 1.4rem;
    font-weight: 900;
}

.profile-tag {
    font-size: 1.1rem;
    color: var(--light-font-color);
}

.profile-creation-date {
    display: flex;
    color: var(--light-font-color);
    align-items: center;
}

.profile-track {
    color: var(--light-font-color);
    display: flex;
    gap: 20px;
    justify-content: flex-start;
}

.profile-track .count {
    font-weight: 600;
    color: var(--font-color);
}

.profile-navbar {
    display: flex;
    border-bottom: 1px solid var(--border-color);
}

.profile-link {
    padding: 0px 16px;
    white-space: nowrap;
    flex: 1;
    font-size: 1.05rem;
    color: var(--light-font-color);
    text-decoration: none;
}

.profile-link.active {
    color: var(--font-color);
    font-weight: 900;
}

.link-wrapper {
    position: relative;
    padding: 16px 0px;
    display: block;
    width: fit-content;
    margin: auto;
}

.active .link-wrapper::after {
    content: '';
    position: absolute;
    height: 4px;
    border-radius: 15px;
    width: 100%;
    bottom: 0px;
    background-color: rgb(0, 162, 255);
    left: 0px;
}

.heightened {
    min-height: 120vh;
}

.profile-info-modal .inputs {
    height: 60px;
    font-size: 1.05rem;
    width: 100%;
    padding: 8px;
    padding-top: 22px;
    background-color: var(--background-color);
    border: 1px solid var(--border-color);
    border-radius: 5px;
    color: var(--font-color);
}

.profile-info-modal {
    display: flex;
    flex-direction: column;
    gap: 24px;
    padding: 10px;
    margin-bottom: 40px;
}

.profile-modal.modal-container {
    align-items: center;
}

.profile-modal.modal {
    margin: 0px;
}

.profile-info-modal .inputs {
    font-family: inherit;
}

.profile-info-modal label {
    position: relative;
}

.inputs:not(:focus)+.label-text.expanded {
    transform: translateY(-50%);
    top: 30px;
    font-size: 1.1rem;
}

.label-text,
.inputs:focus+.label-text {
    position: absolute;
    left: 8px;
    top: 4px;
    color: var(--light-font-color);
    transition: 200ms;
    font-size: .9rem;
}



.letter-count {
    position: absolute;
    right: 4px;
    top: 4px;
    font-size: .95rem;
    color: var(--light-font-color);
    display: none;
}

.inputs:focus {
    outline: 2px solid rgb(0, 162, 255);
}

.inputs:focus~.letter-count {
    display: block;
}

.profile-info-modal .inputs:focus+.label-text {
    color: rgb(0, 162, 255);
}

.profile-bio {
    text-align: left;
}

textarea {
    resize: none;
}

.profile-banner {
    height: 100%;
    object-fit: cover;
    width: 100%;
}

.loadBar-modal {
    transition: 400ms;
    position: absolute;
    height: 4px;
    bottom: -4px;
    width: 100%;
    transform-origin: left;
    left: 0px;
    background-color: rgb(0, 162, 255);
}
.modal-wrapper{
    overflow: auto;
}